// TabContent.js
import React from "react";
import { Box, Typography } from "@mui/material";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import { TABS, TAB_DESCRIPTION } from "../constants/tabs";
import AspectInput from "./AspectInput";
import FormatSelector from "./FormatSelector";
import ImageUploader from "./ImageUploader";
import FotoBackgroundColor from "./FotoBackgroundColor";

const TabContent = ({
  tabKey,
  aspectInput,
  setAspectInput,
  selectedFormat,
  setSelectedFormat,
  onFileChange,
  bgColor,
  setBgColor,
}) => {
  const { t } = useTranslation();
  const navigate = useNavigate();

  const tab = TAB_DESCRIPTION[tabKey] || {};
  const tabFromList = TABS.find((t) => t.key === tabKey);
  const aspect = tabFromList ? tabFromList.aspect : "";

  return (
    <Box sx={{ textAlign: "center", mb: 0, "& > *": { mb: 0 } }}>
      {/* Obrazki */}
      {tab.image && (
          <Box
            sx={{
              width: "100%",
              maxWidth: { xs: 250, sm: 350 },
              minHeight: { xs: 150, sm: 200 },
              margin: "0 auto",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              borderRadius: 2,
              backgroundColor: "transparent",
              padding: 0,
              boxSizing: "border-box",
              boxShadow: "0 0 30px rgba(25, 118, 210, 0.25)"
            }}
          >
            <Box
              component="img"
              src={tab.image}
              alt={t(tabFromList?.labelKey || "")}
              sx={{
                maxWidth: "100%",
                height: "auto",
                objectFit: "contain",
                borderRadius: 2,
                display: "block",
                backgroundColor: "transparent",
              }}
            />
          </Box>
        )}


      {/* Tytu */}
      {tab.title && (
        <Typography
          variant="body1"
          sx={{ mb: 0.5,mt:0.5, fontWeight: "normal", textAlign: "center", lineHeight: 1.2 }}
        >
          {tab.title}
        </Typography>
      )}

      {/* Opis */}
      {/*{tab.description && (
        <Typography
          variant="body1"
          sx={{
            mb: 1,
            fontWeight: 500,
            color: "text.primary",
            maxWidth: 600,
            mx: "auto",
            lineHeight: 1.7,
            textAlign: "justify",
            whiteSpace: "pre-line",
          }}
        >
          {tab.description}
        </Typography>
      )}*/}

      {/* Linki do wymaga i How It Works */}
{tab.link && (
  <Box
    sx={{
      display: "inline-flex",
      alignItems: "center",
      mt: 0.5,
      cursor: "pointer",
      color: "primary.main",
      fontWeight: 500,
      textDecoration: "underline",
      mx: "auto",
      gap: 0.5,
      fontSize: 14,
    }}
  >
    {/* Link do wymaga */}
        <Box
          onClick={() => navigate(tab.link)}
          sx={{ display: "inline-flex", alignItems: "center", gap: 0.5 }}
        >
          {/* <LinkIcon sx={{ fontSize: 16 }} /> */}

          {t("id_link")}
        </Box>

        {/* Pionowa kreska */}
        <Typography sx={{ mx: 1, color: "text.primary" }}>|</Typography>

        {/* Link do How It Works */}
        <Box
          onClick={() => navigate("/how-it-works")}
          sx={{ display: "inline-flex", alignItems: "center", gap: 0.5 }}
        >
          {/* <LinkIcon sx={{ fontSize: 16 }} /> */}
          {t("how_it_works.link_text", "Jak dziaa aplikacja")}
        </Box>
      </Box>
    )}

      {/* Kontener pl */}
        <Box
          sx={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            gap: 2,
            mt: 2,
            mx: "auto",
            width: "100%",
            maxWidth: 220, // wsplna szeroko dla wszystkich pl
            position: "relative",
            left: 13, // przesunicie w prawo o 16px, moesz zmieni warto
          }}
        >
          {/* Format selector */}
          <Box sx={{ display: "flex", justifyContent: "center", width: "100%" }}>
            <FormatSelector
              selectedFormat={selectedFormat}
              setSelectedFormat={setSelectedFormat}
              sx={{ width: "100%" }}
            />
          </Box>

          {/* Niestandardowe pola */}
          {tabKey === "custom" && (
            <>
              <AspectInput
                value={aspectInput}
                onChange={(val) => setAspectInput(val)}
                placeholder={`np. ${aspect}`}
                sx={{ width: "100%" }}
              />
              <FotoBackgroundColor
                color={bgColor || "#ffffff"}
                onChange={setBgColor}
                sx={{ width: "100%" }}
              />
            </>
          )}

          {/* Image uploader */}
          <ImageUploader onChange={onFileChange} sx={{ width: "100%" }} />
        </Box>

    </Box>
  );
};

export default TabContent;
